function ProductModel (sequelize: any, DataType: any): any {
	return sequelize.define('Products', {
		id: {
			type: DataType.INTEGER,
			primaryKey: true,
			autoIncrement: true
		},
		name: {
			type: DataType.STRING,
			allowNull: false,
			validate: {
				notEmpty: true
			}
		},
		description : {
			type: DataType.STRING,
			allowNull: true,
		},
		barCode: {
			type: DataType.STRING,
			allowNull: true,
		}

	});
}

export default ProductModel;